.btn{
  display: inline-block;
  vertical-align: top;
  font-size: 0.875em;
  text-align: center;
  text-decoration: none;
  padding: 15px;
  cursor: pointer;
  .radius;
  box-sizing: border-box;
  .transition;

  &:hover,
  &--hover{

  }

  &:active,
  &--active{

  }

  // Size modificator

  &-size {
    width: 100%;
  }

  &-auto {
    width: auto;
  }

  &-s {
    padding: 5px 15px;
  }

// Color modificator

  &-primary {
    color: @color-text-light;
    background: @color-buttons;
    &:hover,
    &--hover{
      background: @color-buttons-hover;
    }
  }

  &-secondary {
    color: @color-text-light;
    background: @color-forms-primary;
    &:hover,
    &--hover{
      background: @color-forms-secondary;
    }
  }

  &[disabled] {
    background: @color-forms-primary;
  }

}
