CSS

textarea,
input.text,
input[type='text'],
input[type='number'],
input[type='tel'],
input[type='button'],
input[type='submit'],
.input-checkbox {
-webkit-appearance: none;
border: none;
}

textarea {
  font-size: 0.875em;
  padding: 10px;
  border: 1px solid @color-forms-stroke-standart;
  .radius;
  &::placeholder {
    color: @color-text-gray-light;
    .transition-haste;
  }
  &:focus {
    &::placeholder {
      color: #fff;
    }
  }
}

input[type='text'],
input[type='number'],
input[type='tel'] {
  padding: 7px 10px;
  font-size: 0.750em;
  border: 1px solid @color-forms-stroke-standart;
  color: @color-text-gray;
  .radius;
  .transition-haste;
  &::placeholder {
    color: @color-text-gray-light;
    .transition-haste;
  }
  &:focus {
    border-color: @color-forms-stroke-active;
    &::placeholder {
      color: #fff;
    }
  }
}


input[type='file'] {

}

.select {
  height: 34px;
  width: auto;
  -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    padding: 0 40px 0 10px;
  margin-left: 20px;
  font-size: 0.750em;
  background: @color-element-white;
  border-color: @color-forms-stroke-active;
  color: @color-text-gray;
  .radius;
}

.select_family {
  position: relative;
  display: inline-block;
  &::after {
    content: '';
    position: absolute;
    right: 10px;
    top: 50%;
    transform: translateY(-50%);
    border: 3px solid;
    border-bottom-width: 0;
    border-left-color: transparent;
    border-right-color: transparent;
    border-top-color: @color-text-gray;
    margin-left: 10px;
    .transition;
  }
}


.input_family {
  .flex-center-between;
  display: none;
  padding: 20px;
  margin-top: 20px;
  input[type='text'],
  input[type='number'] {
    &:nth-child(2n) {
      margin: 0 20px;
    }
  }
}
